import { Component , ElementRef, ViewChild} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { Camera, CameraOptions } from '@ionic-native/Camera/ngx';
import { ActionSheetController } from '@ionic/angular';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page {

  data: Array<any> = [];
  message: string;

  @ViewChild('pwaphoto', {static: false}) pwaphoto: ElementRef;

  imgURI: string = null;

  constructor(private httpClient: HttpClient) {}

  submit(){
    if(this.message != "" && this.message != undefined){

      this.data.push({ "text": this.message, isAKD: false });

      //"url": "url1",

      this.httpClient.post("https://mohammed.red/reportRumor",
      {"message":  this.message, "language": "ar"})
      .subscribe(
        (data:any)  => {
          console.log("POST Request is successful ", data);
          this.data.push({ "text":data.message, isAKD: true});

        }, error  => { console.log("Error", error);}
        );

      this.message = "";
    }
  }

  eventHandler(keyCode){
    if(keyCode == 13){
      this.submit();
    }
  }


  openPWAPhotoPicker() {
    if (this.pwaphoto == null) {
      return;
    }

    this.pwaphoto.nativeElement.click();
  }

  uploadPWA() {

    if (this.pwaphoto == null) {
      return;
    }

    const fileList: FileList = this.pwaphoto.nativeElement.files;

    if (fileList && fileList.length > 0) {
      this.firstFileToBase64(fileList[0]).then((result: string) => {
        //this.imgURI = result;
        this.data.push({ "src": result, isAKD: false });
        this.httpClient.post("https://mohammed.red/reportRumor",
        {"uri":  result, "language": "ar"})
        .subscribe(
          (data:any)  => {
            console.log("POST Request is successful ", data);
            this.data.push({ "text": data.message, isAKD: true});
  
          }, error  => { console.log("Error", error);}
          );

      }, (err: any) => {
        // Ignore error, do nothing
        this.imgURI = null;
      });
    }
  }

  private firstFileToBase64(fileImage: File): Promise<{}> {
    return new Promise((resolve, reject) => {
      let fileReader: FileReader = new FileReader();
      if (fileReader && fileImage != null) {
        fileReader.readAsDataURL(fileImage);
        fileReader.onload = () => {
          resolve(fileReader.result);
        };

        fileReader.onerror = (error) => {
          reject(error);
        };
      } else {
        reject(new Error('No file found'));
      }
    });
  }
}
